@import url('https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap');
/* css universal color for document */
:root{
  --bodycolor: #000000;

  --colcolor:#31313a;
  
  --redcolor: #ff4800;
  
  --textcolor:#e3e3e3;
}



body {
    margin: 0;
    font-family: 'Josefin Sans', sans-serif !important;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: var(--textcolor) !important;
    background: var(--bodycolor) !important;
    

}

.container-fluid{
    width: 86% !important;
    background: #000;
}
h2{
    font-size: 34px;
    font-weight: 700;
}  
h1{
    font-size: 65px !important;
    font-weight: 700 !important;
    position: relative;
    z-index: 1;
    line-height: 63px;
    padding: 8px 0;
    margin: 0;
    color: var(--textcolor) !important;
}
.Header a:hover{
     color: var(--redcolor);
     transition: ease .5s;
}
.navbar{
    background: var(--bodycolor) !important;
}
.navbar{
    text-transform: uppercase;
    font-weight: bold;
    z-index: 200;
}
.navbar a{
    color: white;
}
.nav-link{
  padding: 1.5rem !important;
}
.navbar-brand{
    font-size: 58px !important;
    font-weight: bold;
    color: white;
	font-family:'Titan One',cursive;
}
.navbar-toggler{
  font-size: 2.25rem !important;
  background-color: var(--textcolor) !important;
}

/*nav underbar style */
.first::before {
  content: "";
  position: absolute;
  top: 70px;
  width: 50px;
  transform: scale(0);
  transform-origin: left;
  height: 3%;
  z-index: -1;
  background: var(--redcolor);
  -webkit-transition: 0.5s ease;
}
  .first:hover::before {
    content: "";
    position: absolute;
    top: 80px;
    transform: scale(1);
    height: 3%;
    z-index: -1;
    background: var(--redcolor);
    -webkit-transition: 0.5s ease;
  }
  
  .second::before {
    content: "";
    position: absolute;
    top: 70px;
    width: 55px;
    transform: scale(0);
    transform-origin: left;
    height: 3%;
    z-index: -1;
    background: var(--redcolor);
    -webkit-transition: 0.5s ease;
  }
    .second:hover::before {
      content: "";
      position: absolute;
      top: 80px;
      transform: scale(1);
      height: 3%;
      z-index: -1;
      background: var(--redcolor);
      -webkit-transition: 0.5s ease;
    }

    .third::before {
      content: "";
      position: absolute;
      top: 70px;
      width: 45px;
      transform: scale(0);
      transform-origin: left;
      height: 3%;
      z-index: -1;
      background: var(--redcolor);
      -webkit-transition: 0.5s ease;
    }
      .third:hover::before {
        content: "";
        position: absolute;
        top: 80px;
        transform: scale(1);
        height: 3%;
        z-index: -1;
        background: var(--redcolor);
        -webkit-transition: 0.5s ease;
      }
      .forth::before {
        content: "";
        position: absolute;
        top: 70px;
        width: 70px;
        transform: scale(0);
        transform-origin: left;
        height: 3%;
        z-index: -1;
        background: var(--redcolor);
        -webkit-transition: 0.5s ease;
      }
        .forth:hover::before {
          content: "";
          position: absolute;
          top: 80px;
          transform: scale(1);
          height: 3%;
          z-index: -1;
          background: var(--redcolor);
          -webkit-transition: 0.5s ease;
        }
        .fifth::before {
          content: "";
          position: absolute;
          top: 70px;
          width: 74px;
          transform: scale(0);
          transform-origin: left;
          height: 3%;
          z-index: -1;
          background: var(--redcolor);
          -webkit-transition: 0.5s ease;
        }
          .fifth:hover::before {
            content: "";
            position: absolute;
            top: 80px;
            transform: scale(1);
            height: 3%;
            z-index: -1;
            background: var(--redcolor);
            -webkit-transition: 0.5s ease;
          }



