/*hero section style */

:root{
    --bodycolor: #000000;
  
    --colcolor:#31313a;
    
    --redcolor: #ff4800;
    
    --textcolor:#e3e3e3;
    --textcolorlighter: rgb(255,255,255,.53);

    --bordercolor: #dee2e621;
  }

.herosection{
    background-color: var(--bodycolor);
    
}
.herosection .row{
    height: 100vh !important;
}
.bgimage{
    background: url('./images/hero.png') no-repeat;
    height: 780px;
    background-repeat: no-repeat;
    background-size: 575px;
    background-position: 100%;
    position: relative;
    margin-top: -90px;

}
.herotext{
    padding-top: 3%;
}
.herotext p{
    font-size: 18px;
    font-weight: 200;
    margin-top: 30px;
}
.herotext h1::before{
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 30px;
    height: 100%;
    z-index: -1;
    background: var(--redcolor);
}
.liquid-button {
    margin: -44px;
    border-radius: 0px !important;
  }
  .liquid-button path {
    cursor: pointer;
  }
  
.btn{
    border: 2px solid var(--redcolor);
    background-color: var(--redcolor) !important;
    border-radius: 0px !important;
    margin-right: 20px;
    margin-top: 30px;
}
.btn Link::before{
    content: "";
    position: absolute;
    height: 0;
    width: 51%;
    background: var(--redcolor);
    z-index: -1;
    -webkit-transition: .5s;
    transition: .5s;
}
.btn a{
    color: white !important;
}
.btn:hover{
    background: #000 !important;
}

/* section user infor and about me */

.cardcolor{
    background: var(--colcolor);
    box-shadow: 10px 10px 15px rgba(255,255,255,.24);
}
.cardbg img{
    height: 300px;
}
.secondcolumn{
    margin-top: 15px;
    margin-bottom: 15px;
}

.avatar img{
    margin-top: -20%;
    width: 150px;
    border: 2px solid white !important;
}
.bordertop{
    border-top:1px solid#dee2e621;
}
.bordertop .col-md-6 span{
    cursor: pointer;
}
.borderbottom{
    border-bottom:1px solid#dee2e621;
}
.borderleft{
    border-left:1px solid#dee2e621;
}
.borderright{
    border-right:1px solid#dee2e621;
}
.headingcolor{
    color: var(--redcolor) !important;
}
.aboutcardsection{
    margin-top: -40px !important;
}

/*button color and background colors */

.btncolor .fa{
    color: var(--textcolor) !important;
}

.info-list ul li{
    padding: 0 0 5px;
    display: inline-block;
    vertical-align: top;
    width: 50%;
    text-align: left;
    letter-spacing: 2px;
    color: var(--textcolorlighter);
    line-height: 1.6;
}
.secondcolumn{
    height: 614px;
}
/* scrollbar style */
.secondcolumn::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

.secondcolumn::-webkit-scrollbar
{
    width: 12px;
    border-radius: 10px;
	background-color: #F5F5F5;
}

.secondcolumn::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #555;
}
.info-list ul li strong{
    color: var(--textcolor) !important;
}
.lighttext{
    color: var(--textcolorlighter);
}
.heavytext{
    color: var(--textcolor) !important;
}
.icon{
    background: linear-gradient(135deg,#ff480026 0%,#ff480024 100%);
    color: var(--redcolor);
}

/* mobile responsive */

/* Small devices (tablets, 768px and up) */
@media (max-width:600px) { 
    .bgimage{
        display: none;
    }
 }