/*nav underbar style */
.first::before {
  content: "";
  position: absolute;
  top: 70px;
  width: 50px;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: left;
          transform-origin: left;
  height: 3%;
  z-index: -1;
  background: #ff4800;
  background: var(--redcolor);
  -webkit-transition: 0.5s ease;
}
  .first:hover::before {
    content: "";
    position: absolute;
    top: 80px;
    -webkit-transform: scale(1);
            transform: scale(1);
    height: 3%;
    z-index: -1;
    background: #ff4800;
    background: var(--redcolor);
    -webkit-transition: 0.5s ease;
  }
  
