/* Animate Background Image */
@-webkit-keyframes mymove {
	0% {
		background-position: 50%;
  }
  100% {
		background-position: 100%;
  }
  }
  
  @keyframes mymove {
	0% {
		background-position: 50%;
  }
  100% {
		background-position: 100%;
  }
  }
/* TEXT GRO SHRINK ANIMATION */

.text {
	list-style: none;
	border-bottom: 0;
  }
  
  .text.hidden {
	border-bottom: 1px solid #ff4800;
  }
  /* header text bg animation */
  
  .text li {
	font: 700 1.2em/1 'Josefin Sans', sans-serif;
	display: inline-block;
	font-weight: 700;
	color: #fff;
	opacity: 1;
	-webkit-transition: all 2s ease-in-out;
	transition: all 2s ease-in-out;
	max-width: 1em;
	text-shadow: 0 0 80px rgba(255, 255, 255, 0.5);
	background: url(data:image/png;base64,) repeat-y;
	-webkit-background-clip: text;
	background-clip: text;
	-webkit-text-fill-color: transparent;
	-webkit-animation: mymove 80s infinite;
  animation: mymove 80s infinite;
  }
  .text.hidden li.spaced {
	padding-left: 0;
  }
  .text li.spaced {
	padding-left:5px;
  }
  
  .text.hidden li.ghost {
	opacity: 0;
	max-width: 0;
  } 

